import React from 'react';
import StockBackend from '../../services/StockBackend/StockBackend';
import { Button } from 'react-bootstrap';

class Home extends React.Component {
    constructor() {
        super();
        this.state = {
            message: 'Message from backend here'
        };

        this.stockBackend = new StockBackend();
        this.getPythonHello = this.getPythonHello.bind(this);
    }

    getPythonHello() {
		this.stockBackend.getTest().then(response => {
            console.log(response);
        });
    }
    
    render () {
        return (
			<Button size="lg" varient="danger" onClick={this.getPythonHello}>
			Retrieve Backend Data!
			</Button>
		);
    }
}

export default Home;